@use "../commons/_mixins.scss" as mixins;

.portal {
  background-color: var(--blue);
  padding: 24px 0;
  @include mixins.tablet {
    padding: 24px 0;
  }
  &__logo {
    color: var(--red);
    @include mixins.flex-center-center;
    gap: 8px;
    & b {
        font-weight: 700;
        font-size: 22px;
    }
    & span {
        padding-top: 2px;
      display: block;
      font-size: 12px;
      font-weight: 400;
      padding-left: 8px;
      position: relative;
      color: var(--white);
      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: -3px;
        width: 1px;
        height: 23px;
        background-color: var(--white);
      }
    }
  }
}
