@use "../commons/_mixins.scss" as mixins;
@use "../commons/_var.scss" as v;

.nav {
    position: fixed;
    left: 102vw;
    top: 0;
    transition: left .3s linear;
    width: 100vw;
    height: 100dvh;
    height: 100vh;
    background-color: var(--white);
    z-index: v.$ui-index-5;
    @include mixins.tablet {
        position: initial;
        height: auto;
        width: auto;
    }
    &.active {
        left: 0;
    }
    &__inner {
        padding: 64px 16px 16px;
        list-style-type: none;
        @include mixins.tablet {
            background-color: var(--blue);
            padding: 13px 16px; 
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            gap: 24px;
        }
    }
    &__item {
        &:not(:first-child) {
            margin-top: 10px;
            @include mixins.tablet {
                margin: 0;
            }
        }
    }
    &__link {
        padding: 4px 4px 3px;
        font-weight: 600;
        line-height: 1.4;
        display: block;
        @include mixins.tablet {
            color: var(--white);
        }
        @include mixins.desktop {
            font-size: 18px;
        }
        @include mixins.hover {
            color: var(--blue);
            border-bottom: 1px solid var(--blue);
            @include mixins.tablet {
                color: var(--white);
                border-bottom: 1px solid var(--white);
            }
        }
        &.active {
            color: var(--blue);
            border-bottom: 1px solid var(--blue);
            @include mixins.tablet {
                color: var(--white);
                border-bottom: 1px solid var(--white);
            }
        }
    }
}