@use "../commons/_mixins.scss" as mixins;
@use "../commons/_var.scss" as v;

.content {
  padding: 30px 0;
  @include mixins.tablet {
    padding: 60px 0;
  }
}
.contacts {
  @include mixins.desktop {
    display: flex;
    gap: 106px;
  }
  &__info {
    @include mixins.desktop {
      align-self: flex-end;
      padding-bottom: 8px;
    }
  }
  &__title {
    font-size: 18px;
    font-weight: 600;
    color: var(--blue);
  }
  &__item {
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    gap: 0 4px;
    &:first-of-type {
      margin-top: 20px;
    }
  }
  &__link {
    font-weight: 600;
    color: var(--blue);
  }
  &__geo {
    @include mixins.flex-center;
    gap: 4px;
    margin-top: 20px;
    font-weight: 600;
    color: var(--red);
    &::before {
      content: "";
      width: 24px;
      height: 24px;
      background: url(../img/icons/geo.svg) center no-repeat;
    }
  }
  &__map {
    margin-top: 30px;
    width: 100%;
    height: 290px;
    @include mixins.desktop {
      margin-top: 0;
      flex: 0 0 852px;
      width: 852px;
    }
  }
}
