@use "../commons/_mixins.scss" as mixins;
@use "../commons/_var.scss" as v;

.header {
    padding: 20px 0 30px;
    &-inner {
        @include mixins.flex-center;
        gap: 36px;
    }
    &__logo {
        @include mixins.flex-center;
        gap: 14px;
        @include mixins.tablet {
            gap: 32px;
        }
        & img {
            display: block;
            flex: 0 0 37px;
            width: 37px;
            @include mixins.tablet {
                flex: 0 0 75px;
                width: 75px;
            }
        }
        & span {
            font-weight: 600;
            font-size: 14px;
            @include mixins.tablet {
                font-size: 24px;
            }
        }
    }
    &__burger {
        margin-left: auto;
        width: 32px;
        height: 32px;
        padding: 0;
        border: 0;
        background-color: transparent;
        background: url(../img/icons/burger.svg) center no-repeat;
        &.active {
            position: fixed;
            top: 9px;
            right: 16px;
            z-index: v.$ui-index-10;
            background: url(../img/icons/close.svg) center no-repeat;
        }
        @include mixins.tablet {
            display: none;
        }
    }
}