@use "../commons/_mixins.scss" as mixins;

.footer {
    position: relative;
    color: var(--white);
    padding: 40px 0; 
    @include mixins.desktop {
        padding: 32px 0 24px;
    }
    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    &__shadow {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    &__inner {
        @include mixins.desktop {
            display: flex;
            justify-content: space-between;
        }
    }
    &__logo {
        max-width: 231px;
        margin: 0 auto;
        @include mixins.desktop {
            order: 1;
            align-self: center;
            margin: 0;
        }
    }
    &__contacts {
        margin-top: 46px;
        @include mixins.desktop {
            order: 3;
            max-width: 333px;
            margin-top: 0;
        }
    }
    &__title {
        font-size: 18px;
        font-weight: 600;
    }
    &__item {
        margin-top: 10px;
        &:first-of-type {
            margin-top: 12px;
        }
    }
    &__info {
        margin-top: 40px;
        @include mixins.desktop {
            margin-top: 0;
            order: 2;
            max-width: 446px;
            align-self: flex-end;
        }
    }
    &__agreement {
        font-size: 14px;
        display: inline-block;
        margin-top: 30px;
        border-bottom: 1px solid var(--white);
    }
}
