@use "../commons/_mixins.scss" as mixins;

.descriptor {
    padding: 60px 0;
    text-align: center;
    position: relative;
    min-height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include mixins.tablet {
        min-height: 547px;
    }
    &__inner {
        color: var(--white);
        display: flex;
        flex-direction: column;
        align-items: center;
        @include mixins.tablet {
            max-width: 889px;
            margin: 0 auto;
        }
    }
    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }
    &__shadow {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    &__title {
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
        @include mixins.tablet {
            gap: 30px;
        }
        @include mixins.desktop {
            flex-direction: row;
            text-align: left;
        }
        & img {
            width: 68px;
            flex: 0 0 68px;
            @include mixins.tablet {
                width: 81px;
                flex: 0 0 81px;
            }
        }
        & span {
            font-weight: 600;
            font-size: 24px;
            @include mixins.tablet {
                font-size: 36px;
            }
        }
    }
    &__text {
        margin-top: 20px;
        @include mixins.flex-center;
        gap: 12px;
        font-weight: 600;
        @include mixins.tablet {
            margin-top: 36px;
        }
    }
    &__link {
        display: inline-block;
        margin-top: 40px;
        padding: 13px 23px;
        border-radius: 3px;
        background-color: var(--red);
        font-size: 14px;
        font-weight: 600;
        @include mixins.tablet {
            margin-top: 36px;
        }
    }
}