@use "./_var.scss" as v;
@use "./_mixins.scss" as mixins;

:root {
  --black: #000000;
  --white: #ffffff;
  --bg-gray: #f8f5f2;
  --red: #e25553;
  --blue: #1e4759;
  --text-dark: #1e1e1e;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: v.$open-sans;
  font-size: 16px;
  font-feature-settings: "tnum";
  font-variant: tabular-nums;
  line-height: 1.4;
  color: var(--text-dark);
}

.wrapper {
  overflow: hidden;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.main {
  flex: 1 0 auto;
}
.container {
  padding: 0 16px;
  width: 100%;
  max-width: 1348px;
  margin: 0 auto;
  box-sizing: border-box;
  @include mixins.tablet {
    padding: 0 24px;
  }
  &--flex {
    @include mixins.flex-center;
  }
}
